*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content{
  
}

.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}

/* transaction page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}

/* antd icons */

.switch-icons{
  border:1px solid rgba(0,0,0,0.677);
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon{
  color:black


}
.inactive-icon{
  color:grey;

}
